.header{
    width: 100%;
    cursor: pointer;
    position: fixed;
    display: flex;
    justify-content: center;
    text-transform: uppercase;
    background: #39445a;
    font-family: "Montserrat", sans-serif;
    font-size: 1.5rem;
    padding: 1rem 0;
    box-shadow: 0 1px 5px black;
    color: white;
    z-index: 100;
}
@media screen and (max-width: 420px) {
    .header{
        font-size: 1.2rem;
        padding: .8rem;
    }
}