*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App{
  min-height: 100vh;
  background: #39445a;
  color: #ffffff;
  padding: 4.8rem 0;
}
.pageTitle{
  text-align: center;
  padding: 1rem 0;
  border-bottom: 1px solid white;
  margin-bottom: 10px;
}
@media screen and (max-width: 420px) {
  .App{
      padding-top: 4rem;
  }
  .pageTitle{
    font-size: 1.2rem;
  }
}

/* bottom navigation start */
.MuiBox-root.css-qxh7fn {
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: #39445a;
  z-index: 999;
  box-shadow: 0 1px 5px black;
}
.css-16lloyr-MuiBottomNavigation-root {
  background-color: #242e40 !important;
}
.css-1ee5err-MuiButtonBase-root-MuiBottomNavigationAction-root.Mui-selected {
  color: #ffffff !important;
}
.css-1ee5err-MuiButtonBase-root-MuiBottomNavigationAction-root {
  color: rgb(255 254 254 / 50%) !important;
}
/* bottom navigation end */
.trending {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

span.MuiChip-label.MuiChip-labelSmall {
  padding: 0 1.5rem;
  color: #ffffff;
  font-weight: 500;
}
.MuiButtonBase-root.MuiChip-root.MuiChip-sizeSmall.MuiChip-clickable {
  background-color: #242e40;
}
.MuiPaginationItem-textPrimary.Mui-selected {
  color: #fff !important;
  background-color: #242e40 !important;
  font-weight: 500 !important;
}
.MuiPaginationItem-root {
  color: #c0c0c0 !important;
}
.css-de1c49 {
  background-color: rgb(36 46 64) !important;
}
.css-fcyteq.Mui-selected {
  color: rgb(255 255 255) !important;
}
.css-1ee5err-MuiButtonBase-root-MuiBottomNavigationAction-root {
